import React from "react"
import {createField, Input, Textarea} from "../../common/FormsControls/FormsControls";
import {reduxForm} from "redux-form";
import classes from "./ProfileInfo.module.css"


const ProfileDataForm = ({profile, handleSubmit, onMainPhotoSelected, error}) => {
    return <form onSubmit={handleSubmit}>
        <h3>Edit you'r info</h3>
        <b>Main Photo:</b>
        <div>
            <input type={"file"} onChange={onMainPhotoSelected}/>
        </div>
        <p>Full Name:</p>
        {createField('Full Name', 'fullName', 'text', Input, [])}
        <p>looking For A Job:</p>
        {createField('lookingForAJob', 'lookingForAJob', 'checkbox', Input, [])}
        <p>My Professional Skills:</p>
        {createField('My Professional Skills', 'lookingForAJobDescription', 'text', Textarea, [])}
        <p>About Me:</p>
        {createField('About Me', 'aboutMe', 'text', Textarea, [])}
        <p>Contacts:</p>
        {Object.keys(profile.contacts).map(key => {
            return <div key={key}>
                {/* todo: create some solution for embedded objects */}
                <p>{key}:</p> {createField(key, "contacts." + key, 'text', Input, [])}
            </div>
        })}
        <button>Save</button>
        {error && <div className={classes.error}><p>{error}</p></div>}
    </form>
}

const ProfileDataFormReduxForm = reduxForm({form: 'edit-profile'})(ProfileDataForm)

export default ProfileDataFormReduxForm

