import React, {useState} from "react";
import classes from "./ProfileInfo.module.css"
import Preloader from "../../common/preloader/preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = ({isOwner, profile, status, updateStatus, savePhoto, saveProfile}) => {

    let [editMode, setEditMode] = useState(false);

    if (!profile) {
        return <Preloader/>
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0])
        }
    }

    const onSubmit =  (formData) => {
         saveProfile(formData).then(() => {
            setEditMode(false)
        })
    }

    return (
        <div>
            <div className={classes.profileBack}>
                <img alt="profileBack" src={process.env.PUBLIC_URL + "/assets/img/back.jpg"}/>
            </div>
            <div>
                <div className={classes.avatar}>
                    <img alt="avatar"
                         src={profile.photos.large != null ? profile.photos.large : process.env.PUBLIC_URL + "/assets/img/avatars/default_user.png"}/>
                </div>
                <div className={classes.discription}>
                    <h3>{profile.fullName}</h3>
                    <ProfileStatusWithHooks isOwner={isOwner} status={status} updateStatus={updateStatus}/>
                    <div>
                    </div>
                    {editMode
                        ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit}
                                           onMainPhotoSelected={onMainPhotoSelected}/>
                        : <ProfileData profile={profile}
                                       isOwner={isOwner}
                                       goToEditMode={() => {
                                           setEditMode(true)
                                       }}

                        />}
                </div>
            </div>
        </div>

    )
}

const ProfileData = ({profile, goToEditMode, isOwner}) => {
    return (
        <>
            {isOwner && <span className={classes.edit} onClick={goToEditMode}>
                <img alt="Settings"
                     src={process.env.PUBLIC_URL + "/assets/img/icons/settings.png"}/></span>}
            <div>
                <p>{profile.lookingForAJobDescription}</p>

                <p>{profile.aboutMe}</p>
                <div className={classes.social}>
                    {Object.keys(profile.contacts).map(key => {
                        if (profile.contacts[key])
                        return (
                            <Contacts contactTitle={key} contactValue={profile.contacts[key]}/>
                            )
                    })}
                </div>
            </div>
        </>
    )
}


const Contacts = ({contactTitle, contactValue}) => {
    return (
                <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={contactValue}><img alt={'icon'} src={process.env.PUBLIC_URL + `/assets/img/${contactTitle}.png`}/></a>

    )
}

export default ProfileInfo